function xorEncrypt({data,key}){if(!key){throw new Error('Key is required');}var dataBytes=Buffer.from(data,'utf8');var keyBytes=Buffer.from(key,'utf8');var resultBytes=Buffer.alloc(dataBytes.length);for(var i=0;i<dataBytes.length;i+=1){resultBytes[i]=dataBytes[i]^keyBytes[i%keyBytes.length];}var result=resultBytes.toString('hex');return result;}function xorDecrypt({encryptedDataHex,key}){if(!key){throw new Error('Key is required');}var encryptedBytes=Buffer.from(encryptedDataHex,'hex');var keyBytes=Buffer.from(key,'utf8');var resultBytes=Buffer.alloc(encryptedBytes.length);for(var i=0;i<encryptedBytes.length;i+=1){resultBytes[i]=encryptedBytes[i]^keyBytes[i%keyBytes.length];}var result=resultBytes.toString('utf8');return result;}export{xorDecrypt,xorEncrypt};