import*as crypto from'crypto';import{entropyToMnemonic,generateMnemonic,mnemonicToEntropy,mnemonicToSeedSync,validateMnemonic,wordlists}from'bip39';import{InvalidMnemonic}from'@onekeyhq/shared/src/errors';import{check}from'@onekeyhq/shared/src/utils/assertUtils';import bufferUtils from'@onekeyhq/shared/src/utils/bufferUtils';function mnemonicToRevealableSeed(mnemonic,passphrase){try{var entropyHexStr=mnemonicToEntropy(mnemonic,wordlists.english);var entropyLength=entropyHexStr.length/2;var seed=mnemonicToSeedSync(mnemonic,passphrase);return{entropyWithLangPrefixed:bufferUtils.bytesToHex(Buffer.concat([Buffer.from([1]),Buffer.from([entropyLength]),Buffer.from(entropyHexStr,'hex'),crypto.randomBytes(32-entropyLength)])),seed:bufferUtils.bytesToHex(seed)};}catch{throw new InvalidMnemonic();}}function revealEntropyToMnemonic(entropyWithLangPrefixed){entropyWithLangPrefixed=bufferUtils.toBuffer(entropyWithLangPrefixed);var langCode=entropyWithLangPrefixed[0];var entropyLength=entropyWithLangPrefixed[1];check(langCode==1&&[16,20,24,28,32].includes(entropyLength),'invalid entropy');return entropyToMnemonic(entropyWithLangPrefixed.slice(2,2+entropyLength),wordlists.english);}export{generateMnemonic,mnemonicToRevealableSeed,mnemonicToSeedSync,revealEntropyToMnemonic,validateMnemonic};