import{payments}from'bitcoinjs-lib';export var toXOnly=function(pubKey){return pubKey.length===32?pubKey:pubKey.slice(1,33);};function isPaymentFactory(payment){return function(script){try{payment({output:script});return true;}catch(err){return false;}};}export var isP2TR=isPaymentFactory(payments.p2tr);export function isTaprootInput(input){return input&&!!(input.tapInternalKey||input.tapMerkleRoot||input.tapLeafScript&&input.tapLeafScript.length||input.tapBip32Derivation&&input.tapBip32Derivation.length||input.witnessUtxo&&isP2TR(input.witnessUtxo.script));}