import*as crypto from'crypto';import{schnorr}from'@noble/curves/secp256k1';import{sha256}from'@noble/hashes/sha256';import*as secp256k1 from'@noble/secp256k1';import{AES_CBC}from'asmcrypto.js';import{bech32}from'bech32';import bufferUtils from'@onekeyhq/shared/src/utils/bufferUtils';export function validateEvent(event){if(!(event instanceof Object))return false;if(typeof event.kind!=='number')return false;if(typeof event.content!=='string')return false;if(typeof event.created_at!=='number')return false;if(!Array.isArray(event.tags))return false;for(var i=0;i<event.tags.length;i+=1){var tag=event.tags[i];if(!Array.isArray(tag))return false;for(var j=0;j<tag.length;j+=1){if(typeof tag[j]==='object')return false;}}return true;}export function serializeEvent(event){if(!validateEvent(event))throw new Error("can't serialize event with wrong or missing properties");return JSON.stringify([0,event.pubkey,event.created_at,event.kind,event.tags,event.content]);}export function getEventHash(event){return bufferUtils.bytesToHex(sha256(serializeEvent(event)));}export function signEvent(event,key){var signedEvent=schnorr.sign(getEventHash(event),key);return bufferUtils.bytesToHex(signedEvent);}export function getNip19EncodedPubkey(pubkey){var words=bech32.toWords(Buffer.from(pubkey,'hex'));return bech32.encode('npub',words,1000);}export function getPrivateEncodedByNip19(privateKey){var words=bech32.toWords(privateKey);return bech32.encode('nsec',words,1000);}export function encrypt(privateKey,pubkey,plaintext){var key=secp256k1.getSharedSecret(privateKey,`02${pubkey}`);var normalizedKey=key.slice(1,33);var iv=crypto.randomBytes(16);var encrypted=AES_CBC.encrypt(Buffer.from(plaintext),normalizedKey,true,iv);return`${Buffer.from(encrypted).toString('base64')}?iv=${Buffer.from(iv.buffer).toString('base64')}`;}export function decrypt(privateKey,pubkey,ciphertext){var key=secp256k1.getSharedSecret(privateKey,`02${pubkey}`);var[cip,iv]=ciphertext.split('?iv=');var normalizedKey=key.slice(1,33);var decrypted=AES_CBC.decrypt(Buffer.from(cip,'base64'),normalizedKey,true,Buffer.from(iv,'base64'));return Buffer.from(decrypted).toString('utf-8');}export function signSchnorr(privateKey,sigHash){var signature=schnorr.sign(Buffer.from(bufferUtils.hexToBytes(sigHash)),privateKey);var signedHex=bufferUtils.bytesToHex(signature);return signedHex;}export function validateNpub(npub){if(!npub.startsWith('npub')){return false;}try{var{prefix,words}=bech32.decode(npub);if(prefix!=='npub'){return false;}var data=bech32.fromWords(words);if(data.length!==32){return false;}return true;}catch(error){return false;}}