import{toBuffer}from'@ethereumjs/util';import*as signUtil from'@metamask/eth-sig-util';import*as ethUtil from'ethereumjs-util';import{isHexString}from'ethjs-util';import{EMessageTypesEth}from'@onekeyhq/shared/types/message';function legacyToBuffer(value){return typeof value==='string'&&!isHexString(value)?Buffer.from(value):toBuffer(value);}var hashMessage=function({messageType,message}){switch(messageType){case EMessageTypesEth.ETH_SIGN:return ethUtil.addHexPrefix(message);case EMessageTypesEth.PERSONAL_SIGN:return ethUtil.addHexPrefix(ethUtil.hashPersonalMessage(legacyToBuffer(message)).toString('hex'));case EMessageTypesEth.TYPED_DATA_V1:return ethUtil.addHexPrefix(signUtil.typedSignatureHash(typeof message==='string'?JSON.parse(message):message));case EMessageTypesEth.TYPED_DATA_V3:return ethUtil.addHexPrefix(signUtil.TypedDataUtils.eip712Hash(typeof message==='string'?JSON.parse(message):message,signUtil.SignTypedDataVersion.V3).toString('hex'));case EMessageTypesEth.TYPED_DATA_V4:return ethUtil.addHexPrefix(signUtil.TypedDataUtils.eip712Hash(typeof message==='string'?JSON.parse(message):message,signUtil.SignTypedDataVersion.V4).toString('hex'));default:throw new Error(`Invalid messageType: ${messageType}`);}};export{hashMessage};